import firebase from 'firebase';
import { size, isEmpty, isDate } from 'lodash';
import { Log } from '../models/Models';
import { USERS } from '../constants/Constants';

export const getDate = ({ date }) => {
  let day = date.getDate() < 10 ? String('0' + date.getDate()) : date.getDate();
  let month = date.getMonth() + 1 < 10 ? String('0' + (date.getMonth() + 1)) : date.getMonth() + 1;
  const year = date.getFullYear();
  return `${year}-${month}-${day}`;
}

export const getTodayDetail = (datetime = new Date()) => {
  let _datetime = datetime;
  if (!isDate(_datetime)) {
    _datetime = new Date(_datetime);
  }
  return {
    year: _datetime.getFullYear(), month: _datetime.getMonth() + 1,
    date: _datetime.getDate(), time: _datetime.getTime(), datetime: _datetime,
  }
}

export const getOrderId = async ({ date }) => {
  let day = date.getDate() < 10 ? String('0' + date.getDate()) : date.getDate();
  let month = date.getMonth() + 1 < 10 ? String('0' + (date.getMonth() + 1)) : date.getMonth() + 1;
  const year = date.getFullYear();
  const datetime = `${year}${month}${day}`;
  const datetimeCheck = `${year}-${month}-${day}`;
  const snapshot = await firebase.database().ref(`/getOrderId/${datetimeCheck}`).once('value');
  const donhangs = snapshot.val();
  const order = size(donhangs) + 1;

  return `${datetime}-${order}`
}

export const formatMoney = (amount, decimalCount = 0, decimal = ".", thousands = ",") => {
  try {
    decimalCount = Math.abs(decimalCount);
    decimalCount = isNaN(decimalCount) ? 2 : decimalCount;

    const negativeSign = amount < 0 ? "-" : "";

    let i = parseInt(amount = Math.abs(Number(amount) || 0).toFixed(decimalCount)).toString();
    let j = (i.length > 3) ? i.length % 3 : 0;

    return negativeSign + (j ? i.substr(0, j) + thousands : '') + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + thousands) + (decimalCount ? decimal + Math.abs(amount - i).toFixed(decimalCount).slice(2) : "");
  } catch (e) {
    console.log(e)
  }
};

