import React, { Component, Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
// import { isEmpty } from 'lodash'
import { Container } from 'reactstrap';
import 'react-toastify/dist/ReactToastify.min.css';
import { logOut } from 'services/SacMauMoiService';
import { AppHeader } from '@coreui/react';
import { ToastContainer, Flip } from 'react-toastify';
import withAuthorization from '../../Helpers/Session/withAuthorization';
import { checkCondition } from '../../services/SacMauMoiService';
import * as ROUTES from 'constants/Routes';

const DefaultHeader = React.lazy(() => import('./DefaultHeader'));
const ThemDonHang = React.lazy(() => import('views/Pages/ThemDonHang/ThemDonHang'));
const QuanLyDonHang = React.lazy(() => import('views/Pages/QuanLyDonHang/QuanLyDonHang'));
const ChiTietDonHang = React.lazy(() => import('views/Pages/QuanLyDonHang/ChiTietDonHang'));
const ThongKe = React.lazy(() => import('views/Pages/ThongKe/ThongKe'));
const LichGiaoHang = React.lazy(() => import('views/Pages/LichGiaoHang/LichGiaoHang'));
const Settings = React.lazy(() => import('views/Pages/Settings/Settings'));

class DefaultLayout extends Component {
  loading = () => <div className="animated fadeIn pt-1 text-center">Loading...</div>

  signOut(e) {
    e.preventDefault()
    logOut();
    this.props.history.push('/login')
  }

  render() {
    return (
      <div className="app">
        <AppHeader fixed>
          <Suspense fallback={this.loading()}>
            <DefaultHeader onLogout={e => this.signOut(e)} />
          </Suspense>
        </AppHeader>
        <div className="app-body">
          <main className="main">
            <Container fluid>
              <Suspense fallback={this.loading()}>
                <Switch>
                  <Route path={ROUTES.addOrder} exact={true} name={'ThemDonHang'} render={props => (<ThemDonHang {...props} firebase={this.props.firebase} />)} />
                  <Route path={ROUTES.orderDetail} name={'ChiTietDonHang'} render={props => <ChiTietDonHang {...props} />} />
                  <Route path={ROUTES.orderManage} exact name={'QuanLyDonHang'} render={props => (<QuanLyDonHang {...props} />)} />
                  <Route path={ROUTES.statistics} name={'ThongKe'} render={props => (<ThongKe {...props} />)} />
                  <Route path={ROUTES.schedulesDelivery} exact={true} name={'LichGiaoHang'} render={props => (<LichGiaoHang {...props} />)} />
                  <Route path={ROUTES.settings} exact={true} name={'Settings'} render={props => (<Settings {...props} />)} />
                  <Redirect from="**" to={ROUTES.addOrder} />
                </Switch>
              </Suspense>
            </Container>
          </main>
        </div>
        <ToastContainer autoClose={2000} transition={Flip} />
      </div>
    );
  }
}

const condition = (authUser) => checkCondition(authUser);

export default withAuthorization(condition)(DefaultLayout);
